:root {
  --darkRed: #8c0d17;
  --darkGrey: #496373;
  --lightGrey: #ced5da;
  --white: #fff;
  --transparent: #8c0d181c;
}

@font-face {
  font-family: Roboto;
  font-style: normal;
  font-weight: 100 1000;
  font-stretch: 25% 151%;
  src: url("RobotoFlex-VariableFont_GRAD,XTRA,YOPQ,YTAS,YTDE,YTFI,YTLC,YTUC,opsz,slnt,wdth,wght.b198d94c.ttf");
}

*, :before, :after {
