}

.profileData {
  background-color: var(--white);
  width: 500px;
  border-radius: 30px;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
  box-shadow: 0 15px 25px #3339;
}

.sectionAvatar {
  width: 90%;
