  align-items: center;
  display: flex;
}

.fotoUserLabel {
  font-weight: 500;
}

.fotoUserLabel:hover {
  color: var(--darkRed);
  cursor: pointer;
  text-decoration: underline;
}

.fotoUserInput {
  width: .1px;
  height: .1px;
  opacity: 0;
  z-index: -10;
  position: absolute;
}

.profileDataSection {
  width: 90%;
}

.formBorder {
  margin-bottom: 25px;
}

.rowProfile {
  border-bottom: 1px solid var(--lightGrey);
  grid-template-columns: 1fr auto;
  padding: 5px 0;
  font-size: 14px;
  display: grid;
}

.rowProfile:hover {
  opacity: .7;
}

.rowProfile:nth-child(6), .rowProfile:last-child {
  margin-bottom: 15px;
}

p[name] {
  font-weight: 700;
}

.editBtn, .cancelBtn {
  color: var(--darkGrey);
  border: 1px solid var(--lightGrey);
  border-radius: 10px;
