}

.btnGroup {
  text-align: end;
}

.activFormName {
  color: var(--darkRed);
  font-weight: 400;
}

.activFormBorder {
  border: 1px solid var(--darkRed);
}

html {
  min-height: 100vh;
  background-color: var(--darkGrey);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

main {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-weight: 300;
  display: flex;
}

.logo {
  width: 23vmin;
  height: auto;
  filter: invert(20%) sepia(1%) saturate(1%) hue-rotate(1deg) brightness(1000%) contrast(80%);
  padding-bottom: 3vh;
}

h1 {
  color: var(--darkRed);
  text-align: center;
  font-variation-settings: "slnt" -10;
  padding-bottom: 1vh;
  font-size: 15vmin;
  font-weight: 700;
  font-stretch: 121%;
}

h3 {
  color: var(--white);
  padding-bottom: 5vh;
  font-size: 7vmin;
  font-weight: 500;
}

button {
  background-color: var(--darkRed);
  border-style: none;
  border-radius: 10px;
  padding: 10px;
  transition: transform .2s;
  box-shadow: 0 5px 15px #3339;
}

button > a {
  color: var(--white);
  font-variant: small-caps slashed-zero;
  font-size: 3vmin;
  font-weight: 500;
}

button:hover {
  transform: scale(1.1);
}

.linkPage {
  padding-top: 25px;
}

.linkPage > a {
  color: var(--white);
  text-align: center;
  padding: 15px;
}

.linkPage > a:hover {
  color: var(--darkRed);
  text-align: center;
  padding: 15px;
}

.formData {
  background-color: var(--white);
  width: 300px;
  border-radius: 30px;
  flex-direction: column;
  padding: 20px;
  display: flex;
  box-shadow: 0 15px 25px #3339;
}

.formData:hover {
  opacity: .97;
}

fieldset {
  border: 1px solid var(--lightGrey);
  border-radius: 30px;
  padding: 20px;
}

legend {
  color: var(--lightGrey);
  text-align: center;
  padding-left: 5px;
  padding-right: 5px;
}

.formData h2 {
  color: var(--darkRed);
  text-align: center;
  margin: 0 0 20px;
  padding: 0;
}

.inputContainer {
  position: relative;
}

.inputContainer .input {
  width: 100%;
  color: var(--darkGrey);
  border: none;
  border-bottom: 1px solid var(--lightGrey);
  background: none;
  outline: none;
  margin-bottom: 30px;
  padding: 10px 0;
  font-size: 16px;
}

.inputContainer .label {
  color: var(--darkRed);
  pointer-events: none;
  padding: 10px 0;
  font-size: 16px;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: 0;
}

.inputContainer .input:focus ~ label, .inputContainer .input:valid ~ label {
  font-size: 12px;
  top: -20px;
  left: 0;
}

.inputContainer .input:focus, .inputContainer .input:valid {
  border-bottom: 1px solid var(--darkRed);
}

.formData > a {
  color: var(--darkGrey);
  text-align: center;
  padding-top: 15px;
}

.formData > a:hover {
  color: var(--darkRed);
}

.logo_error {
  width: 23vmin;
  height: auto;
  filter: invert(20%) sepia(1%) saturate(1%) hue-rotate(1deg) brightness(1000%) contrast(80%);
  padding-top: 5vh;
}

